*{
  padding:0;
  margin:0;
}

.container{
  text-align:center;
  width:40%;
  padding:20px;
  margin:20px auto;
}
.pg{
  display: flex;
  padding:30px;
  justify-content: space-around;
}
.pg button{
  padding:3px 10px;
}
.Person{
  width:40%;
  border: 1px solid black;
  margin:20px auto;
  border-radius: 10px;
  text-align: center;
  padding: 30px;
  color:rgb(249, 224, 224);
  background-color: rgb(180, 173, 164);
}
.PersonImg{
  width:50px;
  height:50px;
  border: 1px solid black;
  border-radius: 50%;
}
.black{
width:100%;
height:50vh;
background-color: black;
}
.white{
  width:100%;
height:50vh;
background-color:white;
text-align: center;
}
.whitediv{
  margin: 0px auto;
  width:50%;
  padding:10px;
  border-radius: 10px;
  border:1px solid black;
  transform: translateY(-100px);
  background-color: white;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;}
.userimg{
  border-radius: 50%;
}
.usertitle,.uservalue{
  font-weight: bold;
  margin: 10px 0px;
}
.values{
  display: flex;
  justify-content: space-around;
}
.values button{
  border:none;
  padding:10px;
  background-color:transparent;
  transition-duration: 500ms;
}
.values button:hover{
  transform: translateY(-5px);
 
}
.btn{
  border:1px solid black;
  border-radius: 10px;;
  padding:10px;
  background-color:transparent;
margin:10px 0px;
}